<!-- *************************************************************************
     TEMPLATE
     ************************************************************************* -->

<template lang="pug">
div(
  :class=`[
    "c-snapchat-profile-box",
    "c-snapchat-profile-box--" + direction,
    "c-snapchat-profile-box--" + size,
    "c-snapchat-profile-box--" + theme,
    {
      "c-snapchat-profile-box--forward": !reverse,
      "c-snapchat-profile-box--reverse": reverse
    }
  ]`
)
  img(
    @click="onGoToSnapchat"
    :src="temporaryAvatar"
    class="c-snapchat-profile-box__avatar"
  )

  .c-snapchat-profile-box__information
    span.c-snapchat-profile-box__name {{ snapchatName }}

    snapchat-button(
      @click="onGoToSnapchat"
      :size="size"
    ) {{ buttonContent }}
</template>

<!-- *************************************************************************
     SCRIPT
     ************************************************************************* -->

<script>
// PROJECT: COMPONENTS
import SnapchatButton from "@/components/snapchat/SnapchatButton"

// PROJECT: DATA
import { snapchatAvatar } from "@/data/snapchat"

// PROJECT: MIXINS
import SnapchatProfileMixin from "@/mixins/widgets/SnapchatProfileMixin"

export default {
  components: {
    SnapchatButton
  },

  mixins: [SnapchatProfileMixin],

  data: () => ({
    // --> STATE <--

    temporaryAvatar: snapchatAvatar
  }),

  methods: {
    // --> EVENT LISTENERS <--

    onGoToSnapchat() {
      let name = this.snapchatName

      // Format name for url
      name = name.toLowerCase()
      name = name.replace(/ /g, "")

      window.open(`https://www.snapchat.com/add/${name}`, "_blank")
    }
  }
}
</script>

<!-- *************************************************************************
     STYLE
     ************************************************************************* -->

<style lang="scss">
// VARIABLES
$c: ".c-snapchat-profile-box";
$sizes: "mini", "small", "default", "medium", "large";

#{$c} {
  display: flex;
  align-items: center;
  justify-content: center;

  #{$c}__avatar {
    cursor: pointer;
  }

  #{$c}__information {
    display: flex;
    align-items: center;
    flex-direction: column;

    #{$c}__name {
      font-weight: 800;
    }
  }

  // --> DIRECTIONS <--

  &--horizontal,
  &--vertical {
    flex-direction: column;
  }

  // --> SIZES <--

  @each $size in $sizes {
    $i: index($sizes, $size) - 1;

    &--#{$size} {
      padding: 14px + (2px * $i);

      #{$c}__avatar {
        width: 60px + (10px * $i);
        height: 60px + (10px * $i);
      }

      #{$c}__information {
        #{$c}__name {
          margin-bottom: 11px + (1px * $i);
          font-size: 13px + (1px * $i);
          line-height: 13px + (1px * $i);
        }
      }

      &#{$c}--horizontal,
      &#{$c}--vertical {
        &#{$c}--forward {
          #{$c}__avatar {
            margin-bottom: 10px + (1px * $i);
          }
        }

        &#{$c}--reverse {
          #{$c}__avatar {
            margin-top: 10px + (1px * $i);
          }
        }
      }
    }
  }

  // --> BOOLEANS <--

  &--reverse {
    #{$c}__avatar {
      order: 1;
    }

    #{$c}__information {
      order: 0;
    }
  }
}

// --> BREAKPOINT: MOBILE WIDE <--

@include mq($from: mobileWide) {
  #{$c} {
    // --> DIRECTIONS <--

    &--horizontal {
      flex-direction: row;

      #{$c}__avatar {
        margin-top: 0 !important; // Disable margin-top set in sizes above
        margin-bottom: 0 !important; // Disable margin-bottom set in sizes above
      }
    }

    // --> SIZES <--

    @each $size in $sizes {
      $i: index($sizes, $size) - 1;

      &--#{$size} {
        &#{$c}--horizontal {
          &#{$c}--forward {
            #{$c}__avatar {
              margin-right: 10px + (1px * $i);
            }
          }

          &#{$c}--reverse {
            #{$c}__avatar {
              margin-left: 10px + (1px * $i);
            }
          }
        }
      }
    }
  }
}
</style>
