  padding: 10px;
  width: calc(100% - 2 * 10px);

  #{$c}__widget {
    #{$c}__box {
      overflow: hidden;
      border-width: 1px;
      border-style: solid;
      border-radius: 6px;
    }
  }

  // --> POSITIONS <--

  &--bottom-left {
    bottom: 0;
    left: 0;

    #{$c}__widget {
      #{$c}__close {
        top: -17px;
        left: 10px;
      }
    }
  }

  &--bottom-right {
    right: 0;
    bottom: 0;

    #{$c}__widget {
      #{$c}__close {
        top: -17px;
        right: 10px;
      }
    }
  }

  &--top-left {
    top: 0;
    left: 0;

    #{$c}__widget {
      #{$c}__close {
        bottom: -17px;
        left: 10px;
      }
    }
  }

  &--top-right {
    top: 0;
    right: 0;

    #{$c}__widget {
      #{$c}__close {
        right: 10px;
        bottom: -17px;
      }
    }
  }

  // --> BOOLEANS <--

  &--editor {
    position: absolute;
  }

  // --> THEMES <--

  @each $theme in $themes {
    $themeName: map-get($theme, "name");

    &--#{$themeName} {
      #{$c}__widget {
        #{$c}__box {
          border-color: mdg($theme, "borders", "default", "primary");
          background-color: mdg($theme, "backgrounds", "default", "primary");
          color: mdg($theme, "fonts", "default", "primary");

          #{$c}__branding {
            box-shadow: 0 -1px 3px mdg($theme, "shadows", "default", "primary");
          }
        }
      }
