<!-- *************************************************************************
     TEMPLATE
     ************************************************************************* -->

<template lang="pug">
button(
  @click="onClick"
  :class=`[
    "c-snapchat-button",
    "c-snapchat-button--" + size
  ]`
)
  slot(
    v-if="$slots.default && $slots.default[0].text.trim()"
  )

  template(
    v-else
  ) Add on Snapchat
</template>

<!-- *************************************************************************
     SCRIPT
     ************************************************************************* -->

<script>
// PROJECT: MIXINS
import SizeMixin from "@/mixins/main/SizeMixin"

export default {
  mixins: [SizeMixin],

  methods: {
    // --> EVENT LISTENERS <--

    onClick(event) {
      this.$emit("click", event)
    }
  }
}
</script>

<!-- *************************************************************************
     STYLE
     ************************************************************************* -->

