  overflow: hidden;
  min-width: 60px;
  outline: none;
  border: 0;
  border-radius: 25px;
  background-color: #fffc00;
  color: #000000;
  vertical-align: middle;
  text-align: center;
  white-space: nowrap;
  font-weight: 800;
  transition: all 125ms linear;
  user-select: none;
  cursor: pointer;

  @include no-tap-highlight-color;

  // --> SIZES <--

  @each $size in $sizes {
    $i: index($sizes, $size) - 1;

    &--#{$size} {
      padding: (9px + (1px * $i)) (12px + (2px * $i));
      font-size: 11px + (1px * $i);
      line-height: 11px + (1px * $i);
      box-shadow: 0 (1px + (1px * $i)) 0 #f1af3e;

      &:active {
        transform: translateY(1px * $i);
